  margin: auto;
  padding: 1rem 1.25rem;
  font-family: sans-serif;
  font-size: 1.5rem;
  text-decoration: none;
  transition: all .3s cubic-bezier(.2, 0, 0, 1);
  position: relative;
}

p > a:after, li > a:after {
  content: "";
  height: 0;
  transform-origin: bottom;
  z-index: -1;
  background-color: #2d2d2d;
  transition: all .3s cubic-bezier(.2, 0, 0, 1);
  display: block;
  position: absolute;
  bottom: 0;
  left: 1.25rem;
  right: 1.25rem;
}

li {
  align-items: center;
  gap: 10px;
  display: flex;
}

li:not(:last-child) {
  margin-bottom: 20px;
}

p > a:hover, li > a:hover {
  color: #fafafa;
}

p > a:hover:after, li > a:hover:after {
  height: 100%;
  left: 0;
  right: 0;
}

img {
  width: 65px;
