  outline: none;
  font-size: 15px;
  transition: border-color .25s ease-in, box-shadow .25s ease-in;
}

input:hover, input:focus {
  border-color: #000;
  box-shadow: 0 8px 15px rgba(0, 0, 0, .25);
}

button {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  min-height: 60px;
  text-align: center;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  touch-action: manipulation;
  will-change: transform;
  background-color: #000;
  border: 2px solid #1a1a1a;
  border-radius: 15px;
  outline: none;
  margin: 0;
  padding: 16px 24px;
